'use strict';

import User from './user.model';
import passport from 'passport';
import config from '../../config/environment';
import jwt from 'jsonwebtoken';
const Mongo = require('mongodb');


const userWithPropertyNotFoundMsg = 'User with this property not found';
const userNotFoundMsg = 'User not found';

// User Model properties
const skillProperty = 'skillsCloud';
const experienceProperty = 'experiences';
const languageProperty = 'languageSkills';
const hobbyProperty = 'hobbies';
const educationProperty = 'education';
const certificationProperty = 'certifications';
const aboutMeProperty = 'aboutMe';
const summaryOfQualificationProperty = 'summaryOfQualification';
const facebookContactProperty = 'facebook';
const twitterContactProperty = 'twitter';
const skypeContactProperty = 'skype';
const linkedinContactProperty = 'linkedIn';

function findUserByUsername(username) {
  return User.findOne({
    username: username
  }).exec();
}

function respondWithBasicProfileResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if (!entity)
      return res.status(404).end();

    return res.status(statusCode).json(entity.profile);
  };
}

function respondWithBasicProfileResultList(res, statusCode) {
  let results = [];
  statusCode = statusCode || 200;
  return function(users) {
    if (!users)
      return res.status(200).json(results);

    for (let i = 0; i < users.length; i++) {
      results.push(users[i].profile);
    }
    return res.status(200).json(results);
  };
}

function respondWithCompleteProfileResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if (!entity) {
      return res.status(404).end();
    }

    sortResults(entity.completeProfile.education, 'startDate', false);
    sortResults(entity.completeProfile.experiences, 'startDate', false);
    sortResults(entity.completeProfile.certifications, 'startDate', false);

    return res.status(statusCode).json(entity.completeProfile);
  };
}

function validationError(res, statusCode) {
  statusCode = statusCode || 422;
  return function(err) {
    return res.status(statusCode).json(getErrorObject(err));
  }
}

function handleUserNotFound(res) {
  return function(entity) {
    if (!entity) {
      return res.status(404).end(getErrorObject(userNotFoundMsg));
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function(err) {
    return res.status(statusCode).send(getErrorObject(err));
  };
}

function addQuerySearchSkill(queryTerms, searchRegExp) {
  queryTerms.push({
    skillsCloud: {
      $elemMatch: {
        skill: {
          $regex: searchRegExp
        }
      }
    }
  });
}

function getQuerySearchSkill(querystring) {
  let query = {},
    searchTerms,
    queryTerms = [],
    searchRegExp;

  //Check if there are query strings
  if (Object.keys(querystring).length !== 0) {
    searchTerms = querystring.q.split(' ');
    for (let i = 0; i < searchTerms.length; i++) {
      searchRegExp = new RegExp('.*' + searchTerms[i] + '.*', 'i');
      addQuerySearchSkill(queryTerms, searchRegExp);
    }
    query = {
      $or: queryTerms
    }
  } else {
    query = null;
  }
  return query;
}

function getQuerySearchAllFields(querystring) {
  let query = {},
    searchTerms,
    queryTerms = [],
    searchRegExp;

  //Check if there are query strings
  if (Object.keys(querystring).length !== 0) {
    searchTerms = querystring.q.split(' ');
    for (let i = 0; i < searchTerms.length; i++) {
      searchRegExp = new RegExp('.*' + searchTerms[i] + '.*', 'i');
      addQuerySearchSkill(queryTerms, searchRegExp);
      queryTerms.push({
        name: {
          $regex: searchRegExp
        }
      }, {
        currentPosition: {
          $regex: searchRegExp
        }
      }, {
        currentClient: {
          $regex: searchRegExp
        }
      }, {
        currentProject: {
          $regex: searchRegExp
        }
      }, {
        experiences: {
          $elemMatch: {
            client: {
              $regex: searchRegExp
            }
          }
        }
      }, {
        experiences: {
          $elemMatch: {
            project: {
              $regex: searchRegExp
            }
          }
        }
      });
    }
    query = {
      $or: queryTerms
    };
  }
  return query;
}

/**
 * Get list of users
 * restriction: 'admin'
 */
export function index(req, res) {
  let query = getQuerySearchAllFields(req.query),
    results = [];

  return User.find().or(query).exec()
    .then(respondWithBasicProfileResultList(res))
    .catch(handleError(res));
}

/**
 * Get a single user
 */
export function show(req, res, next) {
  const username = req.params.username;

  return findUserByUsername(username)
    .then(respondWithCompleteProfileResult(res))
    .catch(err => next(err));
}



/**
 * Get a single user
 */
export function update(req, res, next) {
  let username = req.body.username;

  return User.findOneAndUpdate({
      username: username
    }, req.body, {
      new: true
    }).exec()
    .then(respondWithCompleteProfileResult(res))
    .catch(err => next(err));
}

/**
 * Get my info
 */
export function me(req, res, next) {
  let userId = req.user._id;
  return User.findOne({
      _id: userId
    }).exec()
    .then(respondWithBasicProfileResult(res))
    .catch(err => next(err));
}

/**
 * Search user by skill
 */
export function searchBySkill(req, res, next) {
  let query = getQuerySearchSkill(req.query);
  if (!query) {
    return res.status(200).json([]); // if none skill is passed, we dont return users
  }
  return User.find(query).exec()
    .then(respondWithBasicProfileResultList(res))
    .catch(handleError(res));
}


//  NEW ENDPOINTS AND AUX FUNCTIONS

function getErrorObject(error) {
  return {
    "error": error
  };
}

function findUserPropertyAndUpdateByUserProperty(req, res, action, propertyName) {
  const username = req.params.username;
  const propertyId = req.params.id;
  const propertyValue = req.body.data;
  return findUserByUsername(username)
    .then(handleUserNotFound(res))
    .then(action(res, propertyName, propertyId, propertyValue))
    .catch(handleError(res));

}

function findUserAndCreateUserProperty(req, res, propertyName) {
  const username = req.params.username;
  return findUserByUsername(username)
    .then(handleUserNotFound(res))
    .then(createUserProperty(res, propertyName, req.body.data))
    .catch(handleError(res));
}

function saveEntity(res, entity, returnValue, code) {
  code = code || 200;
  return entity.save(function(err, doc) {
    if (err) return handleError(err);
    return res.status(code).json(returnValue);
  });
}

function updateUserProperty(res, propertyName, propertyId, propertyValue) {
  return function(entity) {
    let property = entity[propertyName].id(propertyId);
    if (!property) {
      return res.status(404).json(getErrorObject(userWithPropertyNotFoundMsg));
    }
    let index = entity[propertyName].indexOf(property);
    entity[propertyName].set(index, propertyValue); //embebbed array must use set
    return saveEntity(res, entity, entity[propertyName][index]);
  };
}

function deleteUserProperty(res, propertyName, propertyId, propertyValue) {
  return function(entity) {
    let property = entity[propertyName].id(propertyId);
    if (!property) {
      return res.status(404).json(getErrorObject(userWithPropertyNotFoundMsg));
    }
    property.remove();
    return saveEntity(res, entity, {}, 204);
  };
}

function createUserProperty(res, propertyName, propertyValue) {
  return function(entity) {
    let property = entity[propertyName];
    property.push(propertyValue);
    return saveEntity(res, entity, property[property.length - 1]);
  };
}

