/**
 * Sort an array of results
 */

function sortResults(arr, field, asc) {
  arr = arr.sort(function(el1, el2) {
    if (asc) {
      return (el1[field] > el2[field]) ? 1 : ((el1[field] < el2[field]) ? -1 : 0);
    } else {
      return (el2[field] > el1[field]) ? 1 : ((el2[field] < el1[field]) ? -1 : 0);
    }
  });
}
